<template>
   <div class="max-w-md bg-gray-200 p-4 mt-8 mx-auto rounded">
       <form action="" class="">
           <div class="flex justify-center items-center gap-4 mx-auto">
               <Button @click.prevent="changeForm('login')">Login</Button>
               <Button @click.prevent="changeForm('registration')">Regestation</Button>
           </div>
           <div class="flex flex-col gap-4 mt-4 mx-auto" v-if="registration">
               <Input placeholder="User Name"/>
               <Input placeholder="Email" type="email"/>
               <Input placeholder="Phone" type="number"/>
               <Input placeholder="Passwodrd" type="password"/>
               <Input placeholder="Confirm Password" type="password"/>
           </div>
           <div class="flex flex-col gap-4 mx-auto mt-4" v-if="login">
               <Input placeholder="Email" type="email"/>
               <Input placeholder="Passwodrd" type="password"/>
           </div>
           <div class="mt-4">
               <Button type="submit">Submit</Button>                 
           </div>
       </form>
   </div>
</template>

<script>
import Input from '../../components/input-text.vue'
import Button from '../../components/btn.vue'
export default {
   components:{
       Input,
       Button
   },
   data() {
    return {
      login: true,
      registration: false
    }
  },
   methods:{
       changeForm(data){
           if(data === 'registration')
           {
               this.registration = true;
               this.login = false
           }
           if(data === 'login')
           {
               this.registration = false;
               this.login = true
           }
       }


   }
}
</script>